html {
  box-sizing: border-box;
}

*, *::after, ::before {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #eee;
  word-break: break-word;
  max-width: 100%;
  font-size: 20px;
  padding: 10px;
  margin: 10px;
}

.main {
  max-width: 5px;
}

